import { UploadOutlined } from '@ant-design/icons';
import { 
   Collapse,
   Upload,
   Form,
   Button
} from "antd";

const CFixAssetImage= ()=>{
    const { Panel } = Collapse;

    const normFile = (e) => {
      if (Array.isArray(e)) {
        return e;
      }
      return e?.fileList;
    };

    
    return (
      <>
         <Collapse defaultActiveKey={["0"]} style={{ marginTop: 10, marginBottom:20  }}>
            <Panel header="Image" key="0">
               <Form.Item
                  name="upload"
                  label="Upload"
                  valuePropName="fileList"
                  getValueFromEvent={normFile}>
                     <Upload name="logo" action="/upload.do" listType="picture">
                     <Button icon={<UploadOutlined />}>Click to upload</Button>
                     </Upload>
               </Form.Item>
            </Panel>
         </Collapse>
      </>
    )
}
export default CFixAssetImage;